//USE CASES Archive page list
.archive-uc {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f7f7f7+0,ffffff+100 */
  background: #f7f7f7; /* Old browsers */
  background: -moz-linear-gradient(top,  #f7f7f7 0%, #ffffff 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top,  #f7f7f7 0%,#ffffff 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom,  #f7f7f7 0%,#ffffff 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f7f7f7', endColorstr='#ffffff',GradientType=0 ); /* IE6-9 */
  .post-preview {
    text-align: left;
    text-decoration: none;
    border-radius: 20px;
    border: 1px solid $gray1;
    color: $blue-gray;
    overflow: hidden;
    margin-bottom: 20px;
    background: $white;
    max-width: 460px;
    position: relative;
    @include media-breakpoint-up(lg) {
      display: flex;
      height: 359px;
      margin-bottom: 40px;
      max-width: unset;
    }
    &:hover {
      color: $blue-gray;
      box-shadow: 2px 20px 45px rgba(7, 14, 39, 0.05);
    }
  }
  .post-preview_link {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  .post-preview_thumb {
    width: 100%;
    height: auto;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    @include media-breakpoint-up(lg) {
      flex: 0 0 460px;
      width: 460px;
    }
    img {
      width: 100%;
      height: auto;
    }
  }
  .post-preview_header {
    color: $mainColor;
    margin-bottom: 20px;
  }
  .post-preview_content {
    padding: 20px;
    @include media-breakpoint-up(lg) {
      padding: 58px 34px;
    }
  }
  .post-preview_excerpt {
    font-size: $fs-rg;
    font-weight: $fw-medium;
    p {
      margin-bottom: 22px;
    }
    ul {
      padding-left: 25px;
    }
  }
  .post-thumbnail {
    width: 100%;
  }
}
.archive-uc_wrap {
  padding: 60px 0 50px;
  text-align: center;
  @include media-breakpoint-up(md) {
    padding: 90px 0 80px;
  }
  @include media-breakpoint-up(lg) {
    padding: 125px 0 120px;
  }
}
.archive-uc_heading {
  margin-bottom: 64px;
}

//All blog posts archive list

.archive-posts {
  background: $bg-color;
  padding: 0 0 20px;
  @include media-breakpoint-up(md){
    padding: 0 0 50px;
  }
  .row  {
    [class*='col'] {
      margin-bottom: 30px;
      @include media-breakpoint-down(md) {
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  .post-preview {
    border-radius: $border-radius;
    overflow: hidden;
    max-width: 400px;
    margin: 0 auto;
    position: relative;
    background: $white;
    border: 1px solid $gray1;
    @include media-breakpoint-up(md) {
      height: 100%;
    }
    &:hover {
      border: 1px solid transparent;
      filter: drop-shadow(0px 35px 45px rgba(7, 14, 39, 0.05));
    }
  }
  .post-preview_link {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  .post-preview_thumb {
    margin: 0;
    img {
      width: 100%;
      height: auto;
    }
  }
  .post-preview_eyebrow {
    text-transform: uppercase;
    text-decoration: none;
    font-weight: $fw-bold;
    font-size: $fs-rg;
    margin-bottom: 12px;
    display: block;
    color: $blue;
    @include media-breakpoint-up(md) {
      margin-bottom: 15px;
    }
  }
  .post-preview_title {
    margin-bottom: 13px;
    color: $deep-blue;
  }
  .post-preview_content {
    padding: 23px 15px;
    @include media-breakpoint-up(md) {
      padding: 34px 19px;
    }
  }
  .post-preview_list {
    .row {
      justify-content: center;
    }
  }
  .post-preview_excerpt {
    color: $blue-gray;
    font-size: $fs-rg;
    font-weight: $fw-medium;
    p {
      line-height: 1.4;
    }
  }
}
.archive-posts_wrap {
  padding: 60px 0 50px;
  @include media-breakpoint-up(md) {
    padding: 90px 0 80px;
  }
  @include media-breakpoint-up(lg) {
    padding: 125px 0 120px;
  }
}
.pagination {
  font-size: $fs-lg;
  font-weight: $fw-medium;
  justify-content: center;
  .page-numbers {
    color: $blue-gray;
    text-decoration: none;
    padding: 2px 4px;
    height: 36px;
    width: 36px;
    border-radius: 4px;
    justify-content: center;
    align-items: center;
    display: flex;
    &:hover {
      color: $mainColor
    }
    &.current {
      color: $white;
      background: $mainColor;
    }
    &.next, &.prev {
      width: auto;
    }
  }
}
.posts-navigation {
  .nav-links {
    a {
      text-decoration: none;
      color: $blue;
      font-size: $fs-lg;
      font-weight: $fw-semibold;
      position: relative;
      padding-right: 30px;
      display: inline-block;
      &:after {
        content: "";
        background: url("../img/arrow-right-big.svg") no-repeat 0 0 / cover;
        height: 16px;
        width: 18px;
        position: absolute;
        top: 0;
        right: 0;
        transition: right ease 0.3s;
        filter: invert(14%) sepia(99%) saturate(3071%) hue-rotate(217deg) brightness(95%) contrast(93%);
        @include media-breakpoint-up(lg){
          height: 24px;
          width: 26px;
          top: 0;
          right: -1px;
        }
      }
      &:hover {
        &:after {
          right: -4px;
        }
      }
    }
  }
}
